import Vue from 'vue'
import Vuex from 'vuex'
import Router from 'vue-router'
import store from '@/store/store.js'
import Props from '@/components/tests/Props.vue'
import Skins from '@/components/tests/Skins.vue'
import Levels from '@/components/tests/Levels.vue'
import Versions from '@/components/tests/Versions.vue'

Vue.use(Router)
Vue.use(Vuex)

const routerOptions = [
  { path: '/', component: 'Home', name: 'home',
  children: [
    { path: '/user/props', component: Props, name: 'userProps' },
    { path: '/user/skins', component: Skins, name: 'userSkins' },
    { path: '/user/levels', component: Levels, name: 'userLevels' },
    { path: '/manage/props', component: Props, name: 'mngProps' },
    { path: '/manage/skins', component: Skins, name: 'mngSkins' },
    { path: '/manage/levels', component: Levels, name: 'mngLevels' },
    { path: '/manage/versions', component: Versions, name: 'mngVersions' }
  ] },
  { path: '/Signup', component: 'Signup', name: 'signup'  },
  { path: '/Login', component: 'Login', name: 'login'  },
  { path: '*', component: 'NotFound', name: '404'  }
]

const routes = routerOptions.map(route => {
  return {
    ...route,
    component: () => import(`@/components/${route.component}.vue`)
  }
})

const router = new Router({
  routes,
  mode: 'history'
})

router.beforeEach((to, from, next) => {
  store.commit('isLogin');
  switch (to.name) {
    case 'login':
    case 'signup':
      break;
    case 'home':
    if (!store.state.LogMsg.isLog){
      next({
        path: '/Login',
        query: {
          redirect: to.fullPath
        } // 将跳转的路由path作为参数，登录成功后跳转到该路由
      })
    }
        break;
    default:
      break;
  }
  next();
})

export default router



