<template>
  <div style="height:100%;">
    <el-table :data="skinlist">
      <el-table-column prop="skinID" label="id" width="80"></el-table-column>
      <el-table-column label="皮肤名">
        <template slot-scope="scope">
          <v-popover placement="bottom" :title="scope.row.skinsName" trigger="hover">
            <el-button type="text">{{scope.row.skinsName}}</el-button>
            <div slot="content">
              {{scope.row.description}}
            </div>
          </v-popover>
        </template>
      </el-table-column>
      <el-table-column prop="skinsValue" label="价值" width="90"></el-table-column>
      <el-table-column label="是否出售" width="150">
        <template slot-scope="scope">
          {{scope.row.isInStore + ""}}
        </template>
      </el-table-column>
      <el-table-column v-if="$route.name!='userSkins'" label="op" width="140">
        <template slot-scope="scope">
          <el-button :disabled="!scope.row.isInStore" type="text" @click="onBuy(scope.row)">购买</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-button v-if="$route.name!='userSkins'" style="margin:12px" @click="onChange">{{btnShow}}</el-button>
  </div>
</template>

<script>
export default {
  data () {
    return {
      skinlist: [],
      btnStatus: false,
      btnShow: "仅看商城"
    }
  },
  watch: {
    '$route' (to, from) {
      let api = "/api/skins";
      if(to.name == 'mngSkins'){
        api += "/all";
      } else if(to.name == 'userSkins') {
        api += "/user/" + this.$store.state.LogMsg.account;
      } else {
        this.$router.push({path: '/'});
      }
      this.$http.get(api).then(res => {
        if(!res.data.code) {
          this.skinlist = res.data.skinlist;
          if(this.skinlist == null || this.skinlist.length == 0) {
            this.$message['warning']("皮肤数量为0");
          }
        } else {
          this.$message['error']("查询失败");
        }
      }).catch(err => {
        console.log(err);
        this.$message['error']("网络错误");
      });
    }
  },
  created() {
    let api = "/api/skins";
    if(this.$route.name == 'userSkins') {
      api += "/user/" + this.$store.state.LogMsg.account;
    } else {
      api += "/all";
    }
    this.$http.get(api).then(res => {
      if(!res.data.code) {
        this.skinlist = res.data.skinlist;
        if(this.skinlist == null || this.skinlist.length == 0) {
          this.$message['warning']("皮肤数量为0");
        }
      } else {
        this.$message['error']("查询失败");
      }
    }).catch(err => {
      console.log(err);
      this.$message['error']("网络错误");
    });
  },
  methods: {
    onBuy(index) {
      let api = "/api/purchase";
      let req = {
        account: this.$store.state.LogMsg.account,
        type:"skin",
        goodId: index.skinID,
        goodNum: 1
      };
      this.$http.post(api, req).then(res => {
        if(!res.data.code) {
          this.$message['success']("购买成功");
          this.$store.commit('goldCoinChange', -index.skinsValue);
        } else if(res.data.code == 1) {
          this.$message['error']("金币不足");
        }  else if(res.data.code == 2) {
          this.$message['error']("已购买该皮肤");
        } else if(res.data.code == 3) {
          this.$message['error']("该皮肤已下架");
        } else {
          this.$message['error']("购买失败");
        }
      }).catch(err => {
        console.log(err);
        this.$message['error']("网络错误");
      });
    },
    onChange() {
      let api = "/api/skins";
      if(this.btnStatus) {
        this.btnShow = "仅看商城";
        api += "/all";
      } else {
        this.btnShow = "查看全部";
        api += "/store";
      }
      this.btnStatus = !this.btnStatus;
      this.$http.post(api).then(res => {
        if(!res.data.code) {
          this.skinlist = res.data.skinlist;
          if(this.skinlist == null || this.skinlist.length == 0) {
            this.$message['warning']("皮肤数量为0");
          }
        } else {
          this.$message['error']("查询失败");
        }
      }).catch(err => {
        console.log(err);
        this.$message['error']("网络错误");
      });
    }
  }
}
</script>

<style scoped>
</style>



