<template>
  <div id="app">
    <v-layout class="app-layout">
      <v-header>
        <v-row class="demo-row">
            <v-col span="18">
              <p style="color:#ddd;font-size:26px;">Traveling Ball Backend test page</p>
            </v-col>
            <v-col span="6">
              <div style="float:right;" v-if="!$store.state.LogMsg.isLog">
                <v-button type="dashed" @click="toLogin" ghost>Login</v-button>
                <v-button type="dashed" @click="toSignup" ghost>Signup</v-button>
              </div>
              <div style="float:right;" v-else>
                <v-popover placement="bottom" :title="$store.state.LogMsg.nickname" trigger="hover">
                  <v-button type="dashed" ghost>{{$store.state.LogMsg.nickname}}</v-button>
                  <div slot="content">
                    <v-row>
                      <v-col span="12"><li>account: </li></v-col>
                      <v-col span="12">
                        <span style="padding-left:8px;">{{$store.state.LogMsg.account}}</span>
                      </v-col>
                      <v-col span="12"><li>nickname: </li></v-col>
                      <v-col span="12">
                        <span style="padding-left:8px;">{{$store.state.LogMsg.nickname}}</span>
                      </v-col>
                      <v-col span="12"><li>goldcoins: </li></v-col>
                      <v-col span="12">
                        <span style="padding-left:8px;">{{$store.state.LogMsg.coins}}</span>
                      </v-col>
                    </v-row>
                  </div>
                </v-popover>
                <v-button type="dashed" @click="Logout" ghost>Logout</v-button>
              </div>
            </v-col>
        </v-row>
      </v-header>
      <v-content class="app-content">
        <div class = "main">
          <router-view/>
        </div>
      </v-content>
      <!-- <v-footer :style="{ textAlign: 'center', color: '#999' }">
        Ant Design ©2016 Created by Ant UED
      </v-footer> -->
    </v-layout>
  </div>
</template>

<script>
export default {
  name: 'App',
  methods: {
    toSignup() {
      this.$router.push({path: '/Signup'});
    },
    toLogin() {
      this.$router.push({path: '/Login'});
    },
    Logout() {
      let api = '/api/logout'
      let req = {
        account: this.$store.state.LogMsg.account
      }
      this.$http.post(api, req).then(res => {
        if (res.data.code) {
          this.$message['error']("登出失败");
        } else {
          let logmsg = {
            isLog: false,
            account: "",
            nickname: "",
            mail: "",
            coins: 0
          }
          this.$store.commit('setLogout', logmsg);
          this.$router.push({path: '/Login'});
        }
      }).catch(err => {
        console.log(err);
        this.$message['error']("网络错误");
        this.$router.push({path: '/Login'});
      });
    }
  }
}
</script>

<style>
#app {
  font-family: "Arial","Microsoft YaHei","黑体","宋体",sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  background: #eee;
  weight: 100%;
  height: 100%;
}

.app-layout {
  weight: 100%;
  height: 100%;
}

.app-content {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.main {
  max-width: 1000px;
  width: 100%;
  height: 100%;
  align-self: center;
  vertical-align: middle;
  text-align: center;
  margin: 12px 8px;
}

.ant-layout-footer {
  padding: 12px 50px;
}

.ant-form-item {
  width: 500px;
}

.demo-row div .ant-btn {
  margin: 0 8px;
}

.ant-card {
  border-radius: 4px;
}

.ant-tabs-tab {
  font-size: 16px;
  font-weight: bold;
  color: #404040;
}

.ant-menu {
  background: #aaa;
  margin: 12px 0;
}

.ant-menu-item span {
  font-size: 14px;
  color: #404040;
}

.ant-popover-inner {
  width: 180px;
}

#home-welcome-page .ant-card-body {
  padding: 0;
  height: 100%;
  width: 100%;
}

#home-welcome-page .ant-layout-content {
  background: #fff;
}

.el-table {
  max-width: 672px;;
}

.el-table th>.cell, .el-table .cell {
  text-align: center;
}
</style>



