import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

const store = new Vuex.Store({
  state: {
    LogMsg: {
      isLog: false,
      account: "",
      nickname: "",
      coins: 0
    }
  },
  mutations: {
    setLogMsg (state, newLogMsg) {
      state.LogMsg = newLogMsg;
      sessionStorage.setItem('LogMsg_account', state.LogMsg.account);
      sessionStorage.setItem('LogMsg_nickname', state.LogMsg.nickname);
      sessionStorage.setItem('LogMsg_coins', state.LogMsg.coins);
    },
    setLogout (state, newLogMsg) {
      state.LogMsg = newLogMsg;
      sessionStorage.removeItem('LogMsg_account');
      sessionStorage.removeItem('LogMsg_nickname');
      sessionStorage.removeItem('LogMsg_coins');
    },
    isLogin (state) {
      if (!state.LogMsg.isLog) {
        state.LogMsg.account = sessionStorage.getItem('LogMsg_account');
        state.LogMsg.nickname = sessionStorage.getItem('LogMsg_nickname');
        state.LogMsg.coins = parseInt(sessionStorage.getItem('LogMsg_coins'));
        if(state.LogMsg.account && state.LogMsg.account != "") {
          state.LogMsg.isLog = true;
        }
      }
    },
    goldCoinChange(state, changeCoinsNum) {
      state.LogMsg.coins += changeCoinsNum;
      sessionStorage.setItem('LogMsg_coins', state.LogMsg.coins);
    }
  }
})
export default store;



