<template>
  <div style="height:100%;">
    <el-table :data="proplist">
      <el-table-column prop="propsID" label="id" width="60"></el-table-column>
      <el-table-column label="道具名">
        <template slot-scope="scope">
          <v-popover placement="bottom" :title="scope.row.propsName" trigger="hover">
            <el-button type="text">{{scope.row.propsName}}</el-button>
            <div slot="content">
              {{scope.row.description}}
            </div>
          </v-popover>
        </template>
      </el-table-column>
      <el-table-column prop="propsValue" label="价值" width="90"></el-table-column>
      <el-table-column label="是否出售" width="100">
        <template slot-scope="scope">
          {{scope.row.isInStore + ""}}
        </template>
      </el-table-column>
      <el-table-column v-if="$route.name=='userProps'" prop="propsNumber" label="数量" width="90"></el-table-column>
      <el-table-column label="op" width="120">
        <template slot-scope="scope">
          <el-button v-if="$route.name=='userProps'" type="text" @click="onUse(scope)">使用</el-button>
          <el-button v-else :disabled="!scope.row.isInStore" type="text" @click="onBuy(scope.row)">购买</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-button v-if="$route.name!='userProps'" style="margin:12px" @click="onChange">{{btnShow}}</el-button>
  </div>
</template>

<script>
export default {
  data () {
    return {
      proplist: [],
      btnStatus: false,
      btnShow: "仅看商城"
    }
  },
  watch: {
    '$route' (to, from) {
      let api = "/api/props";
      if(to.name == 'mngProps'){
        api += "/all";
      } else if(to.name == 'userProps') {
        api += "/user/" + this.$store.state.LogMsg.account;
      } else {
        this.$router.push({path: '/'});
      }
      this.$http.get(api).then(res => {
        if(!res.data.code) {
          this.proplist = res.data.proplist;
          if(this.proplist == null || this.proplist.length == 0) {
            this.$message['warning']("道具数量为0");
          }
        } else {
          this.$message['error']("查询失败");
        }
      }).catch(err => {
        console.log(err);
        this.$message['error']("网络错误");
      });
    }
  },
  created() {
    let api = "/api/props";
    if(this.$route.name == 'userProps') {
      api += "/user/" + this.$store.state.LogMsg.account;
    } else {
      api += "/all";
    }
    this.$http.get(api).then(res => {
      if(!res.data.code) {
        this.proplist = res.data.proplist;
        if(this.proplist == null || this.proplist.length == 0) {
          this.$message['warning']("道具数量为0");
        }
      } else {
        this.$message['error']("查询失败");
      }
    }).catch(err => {
      console.log(err);
      this.$message['error']("网络错误");
    });
  },
  methods: {
    onUse(scope) {
      let api = "/api/useProp";
      let req = {
        account: this.$store.state.LogMsg.account,
        propsId: scope.row.propsID
      };
      this.$http.post(api, req).then(res => {
        if(res.data.code) {
          this.$message['error']("道具不足");
        } else {
          if(--scope.row.propsNumber == 0) {
            this.proplist.splice(scope.$index, 1);
            if(this.proplist == null || this.proplist.length == 0) {
              this.$message['warning']("道具数量为0");
            }
          }
        }
      }).catch(err => {
        console.log(err);
        this.$message['error']("网络错误");
      });
    },
    onBuy(index) {
      let api = "/api/purchase";
      let req = {
        account: this.$store.state.LogMsg.account,
        type:"props",
        goodId: index.propsID,
        goodNum: 1
      };
      this.$http.post(api, req).then(res => {
        if(!res.data.code) {
          this.$message['success']("购买成功");
          this.$store.commit('goldCoinChange', -index.propsValue);
        } else if(res.data.code == 1) {
          this.$message['error']("金币不足");
        } else if(res.data.code == 3) {
          this.$message['error']("该道具已下架");
        } else {
          this.$message['error']("购买失败");
        }
      }).catch(err => {
        console.log(err);
        this.$message['error']("网络错误");
      });
    },
    onChange() {
      let api = "/api/props";
      if(this.btnStatus) {
        this.btnShow = "仅看商城";
        api += "/all";
      } else {
        this.btnShow = "查看全部";
        api += "/store";
      }
      this.btnStatus = !this.btnStatus;
      this.$http.post(api).then(res => {
        if(!res.data.code) {
          this.proplist = res.data.proplist;
          if(this.proplist == null || this.proplist.length == 0) {
            this.$message['warning']("道具数量为0");
          }
        } else {
          this.$message['error']("查询失败");
        }
      }).catch(err => {
        console.log(err);
        this.$message['error']("网络错误");
      });
    }
  }
}
</script>

<style scoped>
#home-welcome-page {
  width: 100%;
  height: 100%;
}

#home-welcome-page .ant-layout {
  height: 100%;
}
</style>



