<template>
  <div style="height:100%;">
    <el-table :data="proplist">
      <el-table-column prop="propsID" label="id" width="60"></el-table-column>
      <el-table-column prop="propsNumber" label="数量" width="90"></el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data () {
    return {
      proplist: []
    }
  },
  created() {
    let api = "/api/props/all";
    this.$http.get(api).then(res => {
      if(!res.data.code) {
        this.proplist = res.data.proplist;
      } else {
        this.$message['error']("查询失败");
      }
    }).catch(err => {
      console.log(err);
      this.$message['error']("网络错误");
    });
  }
}
</script>

<style scoped>
</style>



